<template>
  <div id="todo">
    <div class="container mt-5">
      <div class="row justify-content-md-center">
        <div class="col-md-8 bg-white p-3">
          <div class="d-flex justify-content-between">
              <div class="lead">{{name}}'s To-do list</div>
              <div>
                <input type="checkbox" v-model="hide" class="form-check-input">
                <label class="form-check-label">Hide Completed</label>
              </div>
          </div>

          <div class="row py-2">
            <div class="col-12 col-md-2">
                <button class="btn btn-primary" v-on:click="addNewTodo">Add New</button>
            </div>
            <div class="col">
              <input v-model="newItemText" class="form-control"/>
            </div>
          </div>

            


            <div class="row d-flex justify-content-between px-3 py-2" v-for="l in hiddenList" v-bind:key="l.list">
              <div>{{l.task}}</div>
              <div><input type="checkbox" v-model="l.comp" class="form-check-input">{{l.comp}}</div>
            </div>

            <button class="btn btn-primary btn-sm" v-on:click="deleteItem">
              Delete
            </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      name: "Usman",
      list: [
        
      ],
      hide: true,
      newItemText: ""
    }
  },
   computed:  {
      hiddenList() {
        return this.hide ?
        this.list.filter(l => !l.comp) : this.list
      }
    },
    methods: {
      addNewTodo(){
        this.list.push({
          task: this.newItemText,
          comp: false
        });
        localStorage.setItem("listItems",JSON.stringify(this.list));
        this.newItemText = "";
      },
      deleteItem(){
        this.list = this.list.filter(l=> !l.comp);
        localStorage.setItem('listItems',JSON.stringify(this.list))
      }
    },
    created() {
      let data = localStorage.getItem("listItems");
      if (data != null){
        this.list = JSON.parse(data);
      }
    }
}
</script>

<style>
body{
  background-color: #f8f8f8!important;
}
</style>
